//typography
$font-family-base: Karla, sans-serif
$font-family-secondary: '', sans-serif

$default-font-size: 18px
$default-line-height: 1.4
$default-font-weight: 500
$default-text-color: #fff

$default-transition-duration: 0.3s
$default-transition-function: ease-out
$default-transition: 0.3s ease-out


//breakpoints
$breakpoint-xxl: 1920px    
$breakpoint-xl: 1441px
$breakpoint-lg: 992px
$breakpoint-md: 769px
$breakpoint-sm: 576px
$breakpoint-xs: 425px

$grid-gutter: 20px


//colors

$color-text: #000000
$color-background-main: #f1f1ef
$color-background-light: #ffffff
$color-background-dark: #3e4a56
$color-background-cream: #c7bdb1
$color-background-disabled: #C0B1A2
$color-background-blue: #0ca3e2
$color-background-gold: #FFD700
$color-background-gray: #A5A5A5
$color-gray-border: #F5F5F4

$color-yellow: #edde29
$color-red: #e40d68
$color-blue: #000101
$color-light-gray: #f1f1ef99
$color-white: #fff
$color-disabled: #7E736B

$radius-default: 20px
$default-border: 1px solid $color-background-cream
$shadow-default: 5px 0px 15px rgba(0, 0, 0, 0.1)
$gap-default: 30px


$default-transition: 0.3s all
